const currencyFormatter = new DataformatService();
const fileSize = new FileSize();

export default defineComponent({
  name: 'cib201002sc00',
  components: {
    Timer,
    VueDatePicker,
    FormatInput,
    CurrencyInput,
    PhoneInput,
    BreadCrumb,
    CustomerInformation
  },
  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const getFile = null;
    const getImage = null;
    const fileInfo = {
      file: '',
      fileId: '',
      fileName: '',
      filePath: '',
      fileSize: ''
    }
    return {
      currencyFormatter,
      COMMON_KEY_VALUE,
      exchangeRate: 4000,
      isInvalidInput: false,
      isCountDown: false,
      userID,
      authenticationOTPSMSCode: {
        authenticationCode: '',
        authTransactionDate: '',
        authTransactionSeqNo: '',
        authenticationErrorCount: '',
        successYN: '',
      },
      isViewOnlyAccount: '',
      isFullAccessAccount: '',
      isWrongPhoneNumberFormat: false,
      isWrongEmailFormat: false,
      isInvalidEmail: false,
      isShowEmailErrorText: false,
      isShowOTPSMSCodeErrorText: false,
      isDisableComfirmOTPCodeButton: true,
      isDisableDesignated: [] as boolean[],
      isAllowSendOTP: false,
      isDisableAuthorizationLine: false,
      isExceedMaximumDailyLimit: false,
      isExceedMaximumTransactionLimit: false,
      isComfirmOTPCode: false,
      isNotComfirmOTPCode: false,
      isNotCorrectOTPCode: false,
      isCorrectOTPCode: false,
      isSelectAllData: false,
      isFileEmty: false,
      customerInformation: {} as CustomerInfoInquiryDetail,
      fileList: [] as fileInfo[],
      customerNo,
      getFile,
      getImage,
      fileInfo,
      transferAuthorization: {
        option: '',
        startTime: '',
        endTime: '',
        mondayYN: '',
        tuesdayYN: '',
        wednesdayYN: '',
        thursdayYN: '',
        fridayYN: '',
        saturdayYN: '',
        sundayYN: ''
      },
      isAllowDesignatedHour: false,
      designatedHour: {
        startTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        endTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        transferDay: {
          mondayYN: false,
          tuesdayYN: false,
          wednesdayYN: false,
          thursdayYN: false,
          fridayYN: false,
          saturdayYN: false,
          sundayYN: false
        }
      },
      tempDesignatedHour: {
        startTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        endTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        transferDay: {
          mondayYN: false,
          tuesdayYN: false,
          wednesdayYN: false,
          thursdayYN: false,
          fridayYN: false,
          saturdayYN: false,
          sundayYN: false
        }
      },
      customerBasicInfo: {
        userID: '',
        mainTransferAccount: '',
        feeWithdrawalAccount: '',
        email: '',
      },
      preWithdrawalAccounts: [] as preWithdrawalAccount[],
      mainTransferAccounts: [] as preWithdrawalAccount[],
      authLineOption: [] as { key: string, value: string }[],
      authLinePolicyOption: [] as { key: string, value: string }[],
      authLineRoleOption: [] as { key: string, value: string }[],
      isInvalidManagementAccount: [] as boolean[],
      securityMedia: {
        mediaType: '01',
        securityMediaNo: '',
        authenticationCode: '',
        isAuth: true
      },
      designatedAccountListForRegister: [] as designatedAccountListRegister[],
      managementAccount: {
        accountManagementTypeCode: '01',
        designatedAccountList: [] as { accountNo: string, accountTypeCode: string, designatedAccountTypeCode: string, isInquiry: boolean, isWithdraw: boolean, currencyCode: string }[]
      },
      companyAuthorizationLine: {
        authLineOption: '01',
        authLinePolicyOption: '01',
        authLineRoleOption: '01'
      },
      transferLimit: {
        maxBankTransactionLimit: 0,
        maxBankDailyLimit: 0,
        transactionLimitAmount: 0,
        dailyLimitAmount: 0
      },
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      isChangeFromStartTime: false,
      isChangeFromEndTime: false,
      transferAuthorizationCode: '',
      isChangeTransferDay: false,
      isAllowDisSelect: false,
      isfirstTransferAuthorization:'01',
      isSecondTransferAuthorization:'',
      isthirdTransferAuthorization:'',
    }
  },

  computed: {
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
    },
    formattedStartTime(): string {
      return this.$datetime.formatVueTimePicker(this.designatedHour.startTime);
    },
    formattedStartTime1() {
      return (accountTypeCode: any) => this.$datetime.formatVueTimePicker(accountTypeCode);
    },
    formattedEndTime(): string {
      return this.$datetime.formatVueTimePicker(this.designatedHour.endTime);
    },
    transactionLimitAmountKHR(): number {
      return this.transferLimit.transactionLimitAmount * this.exchangeRate;
    },
    dailyLimitAmountKHR(): number {
      return this.transferLimit.dailyLimitAmount * this.exchangeRate;
    },
    transactionRemainingLimit(): number {
      return this.transferLimit.maxBankTransactionLimit - this.transferLimit.transactionLimitAmount;
    },
    dailyRemainingLimit(): number {
      return this.transferLimit.maxBankDailyLimit - this.transferLimit.dailyLimitAmount;
    },
    transactionRemainingLimitKHR(): number {
      return this.transactionRemainingLimit * this.exchangeRate;
    },
    dailyRemainingLimitKHR(): number {
      return this.dailyRemainingLimit * this.exchangeRate;
    },
    bankMaximumTransactionLimit(): string {
      return currencyFormatter.formatCurrency(this.transferLimit.maxBankTransactionLimit, "USD");
    },
    bankMaximumTransactionLimitKHR(): string {
      return currencyFormatter.formatCurrency(this.transferLimit.maxBankTransactionLimit * this.exchangeRate, "KHR");
    },
    bankMaximumDailyLimit(): string {
      return currencyFormatter.formatCurrency(this.transferLimit.maxBankDailyLimit, "USD");
    },
    bankMaximumDailyLimitKHR(): string {
      return currencyFormatter.formatCurrency((this.transferLimit.maxBankDailyLimit * this.exchangeRate), "KHR");
    }
  },

  watch: {
    'designatedHour.transferDay.mondayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'

        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.tuesdayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'
        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.wednesdayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'
        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.thursdayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'
        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.fridayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'
        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.saturdayYN'(newValue) {
      if (newValue == false) {
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorization.option = '03'
        if (this.isSelectAllData == true) {
          this.isSelectAllData = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    'designatedHour.transferDay.sundayYN'(newValue) {
      if (this.transferAuthorization.option == '01') {
        if (newValue == true) {
          this.isChangeTransferDay = false
        } else {
          this.isChangeTransferDay = true
          this.transferAuthorization.option = '03'
          if (this.isSelectAllData == true) {
            this.isSelectAllData = false
          }
        }
      } else if (this.transferAuthorization.option == '02') {
        if (newValue == false) {
          this.isChangeTransferDay = false
          if (this.isSelectAllData == true) {
            this.isSelectAllData = false
          }
        } else {
          this.handleValidateTransferAuthorization(this.designatedHour)
          this.isChangeTransferDay = true
          this.isSelectAllData = true
          this.transferAuthorization.option = '03'
        }
      } else if (this.transferAuthorization.option == '03') {
        this.isSelectAllData = false
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    isSelectAllData(newValue) {
      if (newValue) {
        if (this.transferAuthorization.option == '01') {
          if (newValue == true) {
            this.isChangeTransferDay = false
            this.isAllowDisSelect = false
          } else {
            if (this.designatedHour.transferDay.mondayYN == true || this.designatedHour.transferDay.tuesdayYN == true || this.designatedHour.transferDay.wednesdayYN == true
              || this.designatedHour.transferDay.thursdayYN == true
              || this.designatedHour.transferDay.fridayYN == true
              || this.designatedHour.transferDay.saturdayYN == true
              || this.designatedHour.transferDay.sundayYN == true) {
              this.designatedHour.transferDay.mondayYN = false,
                this.designatedHour.transferDay.tuesdayYN = false,
                this.designatedHour.transferDay.wednesdayYN = false,
                this.designatedHour.transferDay.thursdayYN = false,
                this.designatedHour.transferDay.fridayYN = false,
                this.designatedHour.transferDay.saturdayYN = false,
                this.designatedHour.transferDay.sundayYN = false
            } else {
              this.isChangeTransferDay = true
              this.transferAuthorization.option = '03'
            }
          }
        } else if (this.transferAuthorization.option == '02') {
          if (newValue == false) {
            this.isChangeTransferDay = false;
          } else {
            this.isChangeTransferDay = true
            this.transferAuthorization.option = '03'
            this.designatedHour.transferDay.mondayYN = true,
              this.designatedHour.transferDay.tuesdayYN = true,
              this.designatedHour.transferDay.wednesdayYN = true,
              this.designatedHour.transferDay.thursdayYN = true,
              this.designatedHour.transferDay.fridayYN = true,
              this.designatedHour.transferDay.saturdayYN = true,
              this.designatedHour.transferDay.sundayYN = true 
          }
        } else {
          if (newValue == true) {
            if(this.isfirstTransferAuthorization == '01'){
            const startimesun = '000000000';
            let startTimeConvert = '';
            startTimeConvert = this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime)
            if((startTimeConvert == startimesun) && (this.designatedHour.endTime.hours == 23 && this.designatedHour.endTime.minutes == 59 && this.designatedHour.endTime.seconds == 0) && this.isSelectAllData){
              this.transferAuthorization.option = '01';
            }
            this.isChangeTransferDay = true;
            this.designatedHour.transferDay.mondayYN = true,
              this.designatedHour.transferDay.tuesdayYN = true,
              this.designatedHour.transferDay.wednesdayYN = true,
              this.designatedHour.transferDay.thursdayYN = true,
              this.designatedHour.transferDay.fridayYN = true,
              this.designatedHour.transferDay.saturdayYN = true,
              this.designatedHour.transferDay.sundayYN = true
            }else if(this.isSecondTransferAuthorization == '02'){
              this.designatedHour.transferDay.mondayYN = true,
              this.designatedHour.transferDay.tuesdayYN = true,
              this.designatedHour.transferDay.wednesdayYN = true,
              this.designatedHour.transferDay.thursdayYN = true,
              this.designatedHour.transferDay.fridayYN = true,
              this.designatedHour.transferDay.saturdayYN = true,
              this.designatedHour.transferDay.sundayYN = true
            }else if(this.isthirdTransferAuthorization == '03'){
              this.isAllowDisSelect = true
              this.designatedHour.transferDay.mondayYN = true,
              this.designatedHour.transferDay.tuesdayYN = true,
              this.designatedHour.transferDay.wednesdayYN = true,
              this.designatedHour.transferDay.thursdayYN = true,
              this.designatedHour.transferDay.fridayYN = true,
              this.designatedHour.transferDay.saturdayYN = true,
              this.designatedHour.transferDay.sundayYN = true
            }
           
          }
        }
      }

      else {
        if ((this.transferAuthorization.option == '03' && this.isAllowDisSelect == true) || this.transferAuthorization.option == '01') {
          if (this.designatedHour.transferDay.mondayYN == true || this.designatedHour.transferDay.tuesdayYN == true || this.designatedHour.transferDay.wednesdayYN == true
            || this.designatedHour.transferDay.thursdayYN == true
            || this.designatedHour.transferDay.fridayYN == true
            || this.designatedHour.transferDay.saturdayYN == true
            || (this.designatedHour.transferDay.sundayYN == true || this.designatedHour.transferDay.sundayYN == false)) {
            this.designatedHour.transferDay.mondayYN = false,
              this.designatedHour.transferDay.tuesdayYN = false,
              this.designatedHour.transferDay.wednesdayYN = false,
              this.designatedHour.transferDay.thursdayYN = false,
              this.designatedHour.transferDay.fridayYN = false,
              this.designatedHour.transferDay.saturdayYN = false,
              this.designatedHour.transferDay.sundayYN = false
            this.isAllowDisSelect = false
          }
        } else if (this.transferAuthorization.option == '02' && this.isSelectAllData == false) {
          this.isAllowDisSelect = false;
        }
        else if(this.isSecondTransferAuthorization == '02'){
          if (this.designatedHour.transferDay.mondayYN == true && this.designatedHour.transferDay.tuesdayYN == true && this.designatedHour.transferDay.wednesdayYN == true
            && this.designatedHour.transferDay.thursdayYN == true
            && this.designatedHour.transferDay.fridayYN == true
            && this.designatedHour.transferDay.saturdayYN == true
            && (this.designatedHour.transferDay.sundayYN == true)) {
          this.designatedHour.transferDay.mondayYN = false,
              this.designatedHour.transferDay.tuesdayYN = false,
              this.designatedHour.transferDay.wednesdayYN = false,
              this.designatedHour.transferDay.thursdayYN = false,
              this.designatedHour.transferDay.fridayYN = false,
              this.designatedHour.transferDay.saturdayYN = false,
              this.designatedHour.transferDay.sundayYN = false
            }else{
              this.isChangeTransferDay = true
            }
        }else if(this.isfirstTransferAuthorization == '01'){
          if (this.designatedHour.transferDay.mondayYN == true && this.designatedHour.transferDay.tuesdayYN == true && this.designatedHour.transferDay.wednesdayYN == true
            && this.designatedHour.transferDay.thursdayYN == true
            && this.designatedHour.transferDay.fridayYN == true
            && this.designatedHour.transferDay.saturdayYN == true
            && (this.designatedHour.transferDay.sundayYN == true)) {
          this.designatedHour.transferDay.mondayYN = false,
              this.designatedHour.transferDay.tuesdayYN = false,
              this.designatedHour.transferDay.wednesdayYN = false,
              this.designatedHour.transferDay.thursdayYN = false,
              this.designatedHour.transferDay.fridayYN = false,
              this.designatedHour.transferDay.saturdayYN = false,
              this.designatedHour.transferDay.sundayYN = false
            }else{
              this.isChangeTransferDay = true
            }
        }

      }
    },

    'designatedHour.startTime': function (newValue) {
      if (this.transferAuthorization.option == '01') {
        this.tempDesignatedHour = this.designatedHour
        this.isSelectAllData = true
        if (newValue.hours != 0 || newValue.minutes != 0 || newValue.seconds != 0) {
          this.isChangeFromStartTime = true
          this.transferAuthorization.option = '03'
          this.transferAuthorizationCode = '01'
          this.designatedHour = this.tempDesignatedHour
        }
      } else if (this.transferAuthorization.option == '02') {
        this.tempDesignatedHour = this.designatedHour
        this.isSelectAllData = false
        // this.isChangeFromStartTime =true
        this.transferAuthorizationCode = '02'
        if (newValue.hours != 8 || newValue.minutes != 0 || newValue.seconds != 0) {
          this.transferAuthorization.option = '03'
          this.isChangeFromStartTime = true

        } else {
          this.isChangeFromStartTime = false
        }
      } else {
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.endTime': function (newValue) {
      if (this.transferAuthorization.option == '01') {
        this.tempDesignatedHour = this.designatedHour
        this.isSelectAllData = true
        if (newValue.hours != 23 || newValue.minutes != 59 || newValue.seconds != 59) {
          this.isChangeFromEndTime = true
          this.transferAuthorization.option = '03'
          this.transferAuthorizationCode = '01'
        }
        else {
          this.isChangeFromEndTime = false
          //  this.handleValidateTransferAuthorization(this.designatedHour)
        }
      }
      else if (this.transferAuthorization.option == '02') {
        this.tempDesignatedHour = this.designatedHour
        if (newValue.hours != 18 || newValue.minutes != 0 || newValue.seconds != 0) {
          this.transferAuthorization.option = '03'
          this.isChangeFromStartTime == true ? this.transferAuthorization.option = '03' : this.transferAuthorization.option
          this.isChangeFromEndTime = true
        } else {
          this.isChangeFromEndTime = false
        }
      } else if (this.transferAuthorization.option == '03') {
        const startimesat = '080000000';
        const startimesun = '000000000';
        const startTimeConvert = this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime)
        if ((startTimeConvert == startimesun) && (this.designatedHour.endTime.hours == 23 && this.designatedHour.endTime.minutes == 59 && this.designatedHour.endTime.seconds == 0) && this.designatedHour.transferDay.mondayYN && this.designatedHour.transferDay.tuesdayYN && this.designatedHour.transferDay.wednesdayYN && this.designatedHour.transferDay.thursdayYN && this.designatedHour.transferDay.fridayYN && this.designatedHour.transferDay.saturdayYN && this.designatedHour.transferDay.sundayYN) {
          this.transferAuthorization.option = '01'
        } else if ((startTimeConvert == startimesat) && (this.designatedHour.endTime.hours == 18 && this.designatedHour.endTime.minutes == 0 && this.designatedHour.endTime.seconds == 0) && this.designatedHour.transferDay.mondayYN && this.designatedHour.transferDay.tuesdayYN && this.designatedHour.transferDay.wednesdayYN && this.designatedHour.transferDay.thursdayYN && this.designatedHour.transferDay.fridayYN && this.designatedHour.transferDay.saturdayYN && !this.designatedHour.transferDay.sundayYN) {
          this.transferAuthorization.option = '02'
        }
      }
    },

    'managementAccount.accountManagementTypeCode': function (designatedAccountTypeCode) {
      if (this.approvalType == '') {
        this.isDisableDesignated = []
        switch (designatedAccountTypeCode) {
          case "01":
            this.handleAccountManagement('01', this.customerBasicInfo.mainTransferAccount)
            break;
          case "02":
            this.handleAccountManagement(designatedAccountTypeCode, this.customerBasicInfo.mainTransferAccount)
            break;
        }
      }
    },

    'customerBasicInfo.mainTransferAccount': function (newmainTransferAccountValue) {
      this.isDisableDesignated = [];
      if (this.managementAccount.accountManagementTypeCode == '02') {
        this.handleAccountManagement('02', newmainTransferAccountValue)
      }
    },

    'securityMedia.securityMediaNo': function (newValue) {
      if (this.securityMedia.mediaType == '01' && newValue.length == 0) {
        this.isWrongPhoneNumberFormat = false;
      } else if (this.securityMedia.mediaType == '01' && newValue.length == 9 || newValue.length > 9) {
        this.isWrongPhoneNumberFormat = false;
        this.isAllowSendOTP = true;
      } else if (this.securityMedia.mediaType == '01' && newValue.length < 9) {
        this.isWrongPhoneNumberFormat = true;
        this.isAllowSendOTP = false;
      } else if (this.securityMedia.mediaType == '02' && newValue) {
        this.isWrongEmailFormat = false;  // if there're values please let the isWrongEmailFormat false and after click send again it will display the error text
      }
    },

    'customerBasicInfo.email': function (newValue) {
      this.isInvalidEmail = this.$validate.validateEmail(newValue);
      if (newValue && !this.isInvalidEmail) {
        this.isShowEmailErrorText = false;
      }
    },

    'authenticationOTPSMSCode.authenticationCode': function (newAuthenticationCodeValue, oldAuthenticationCodeValue) {
      if (newAuthenticationCodeValue) {
        this.isShowOTPSMSCodeErrorText = false;
        this.isNotComfirmOTPCode = false;
      }
      else {
        this.isShowOTPSMSCodeErrorText = true;
        if (this.isComfirmOTPCode) { // if OTP comfirmed and valid value it will display value and set isShowOTPSMSCodeErrorText = false
          this.authenticationOTPSMSCode.authenticationCode = oldAuthenticationCodeValue;
          this.isShowOTPSMSCodeErrorText = false;
        }
      }
    },

    'authenticationOTPSMSCode.authTransactionSeqNo': function (newValue) {
      if (newValue) {
        this.isDisableComfirmOTPCodeButton = false;
      }
    },

    'companyAuthorizationLine.authLineOption': function (newValue) {
      if (newValue == '02') {
        this.isDisableAuthorizationLine = true;
        this.companyAuthorizationLine.authLinePolicyOption = '01';
        this.companyAuthorizationLine.authLineRoleOption = '01';
      } else {
        this.isDisableAuthorizationLine = false;
      }
    },

    transactionRemainingLimit(transactionRemainingValue) {
      if (transactionRemainingValue < 0) {
        this.isExceedMaximumTransactionLimit = true;
      } else {
        this.isExceedMaximumTransactionLimit = false;
      }
    },

    dailyRemainingLimit(dailyRemainingValue) {
      if (dailyRemainingValue < 0) {
        this.isExceedMaximumDailyLimit = true;
      } else {
        this.isExceedMaximumDailyLimit = false;
      }
    }
  },

  async created() {
    if (this.approvalType == '') {
      this.getCustomerInfo(`${this.customerNo}`);
      this.getPreTransferLimit();
      this.handleGetCommonCodeValue();
      this.getPreWithdrawalAccount();
      this.transferAuthorization.option = '01';

      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('235959999'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: true
        }
      }
    } else {
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent);
        this.approvalRes = authDetail.body;
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        await this.getCustomerInfo(`${authDetailParsed.userInfo.customerNo}`);
        await this.getPreTransferLimit();

        this.transferAuthorization.option = authDetailParsed.transferAuthorizationHour.operationServiceCode
        this.designatedHour.startTime = this.$datetime.convertTimeStringToTimeObject(authDetailParsed.transferAuthorizationHour.operationServiceStartTime);
        this.designatedHour.endTime = this.$datetime.convertTimeStringToTimeObject(authDetailParsed.transferAuthorizationHour.operationServiceEndTime);

        this.designatedHour.transferDay.mondayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationMondayYN)
        this.designatedHour.transferDay.tuesdayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationTuesdayYN)
        this.designatedHour.transferDay.wednesdayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationWednesdayYN)
        this.designatedHour.transferDay.thursdayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationThursdayYN)
        this.designatedHour.transferDay.fridayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationFridayYN)
        this.designatedHour.transferDay.saturdayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationSaturdayYN)
        this.designatedHour.transferDay.sundayYN = this.stringToBoolean(authDetailParsed.transferAuthorizationHour.operationSundayYN)

        this.customerBasicInfo.userID = authDetailParsed.userInfo.userID;
        this.customerBasicInfo.email = authDetailParsed.userInfo.email;
        this.securityMedia.securityMediaNo = authDetailParsed.userInfo.securityMediaNo;
        this.customerBasicInfo.mainTransferAccount = authDetailParsed.userInfo.mainTransferAccount;
        this.customerBasicInfo.feeWithdrawalAccount = authDetailParsed.userInfo.feeWithdrawalAccountNo;

        COMMON_KEY_VALUE.subscriptionauthLineOption.map((item) => {
          item.key == authDetailParsed.userInfo.subscriptionApprovalTypeCode
          return item
        });

        this.mainTransferAccounts.push({
          accountNo: this.customerBasicInfo.mainTransferAccount,
          accountName: '',
          currencyCode: '',
          accountTypeCode: ''
        });

        this.preWithdrawalAccounts.push({
          accountNo: authDetailParsed.userInfo.feeWithdrawalAccountNo,
          accountName: '',
          currencyCode: '',
          accountTypeCode: ''
        });

        this.managementAccount.accountManagementTypeCode = authDetailParsed.userInfo.accountManagementTypeCode;
        this.managementAccount.designatedAccountList = authDetailParsed.designatedAccountList;

        this.managementAccount.designatedAccountList.map((item) => {
          if (this.managementAccount.accountManagementTypeCode == '01') {
            item.isInquiry = false;
            item.isWithdraw = true;
          } else if (this.managementAccount.accountManagementTypeCode == '02') {
            item.designatedAccountTypeCode == '01' ? item.isWithdraw = true : item.isWithdraw = false;
            item.designatedAccountTypeCode == '02' ? item.isInquiry = true : item.isInquiry = false;
            return item;
          }
        });

        authDetailParsed.transactionLimitList.map((item) => {
          this.transferLimit.transactionLimitAmount = item.transactionLimitAmount
          this.transferLimit.dailyLimitAmount = item.dailyLimitAmount
        });

        this.handleGetCommonCodeValue();

        this.companyAuthorizationLine.authLineOption = authDetailParsed.userInfo.subscriptionApprovalTypeCode
        this.companyAuthorizationLine.authLinePolicyOption = authDetailParsed.userInfo.authorizationLinePolicyTypeCode
        this.companyAuthorizationLine.authLineRoleOption = authDetailParsed.userInfo.authorizationLineAssignTypeCode

        authDetailParsed.fileList.map((item) => {
          this.fileInfo.fileId = item.fileId
        });
        
        if(this.fileInfo.fileId){
          const responseFileInfo = await this.$file.getFileInfo(this.fileInfo.fileId);
          this.fileInfo.fileName = responseFileInfo.data.body.originalFileName
          this.fileInfo.fileId = responseFileInfo.data.body.fileId
        }
      });
    }
  },

  methods: {
    handleValidateTransferAuthorization(transferAuthorizationData) {
      const startimesun = '000000000';
      const endtimesun = '235959000';
      const startimesat = '080000000';
      const endtimesat = '180000000';

      const startTimeConvert = this.$datetime.convertTimeObjectToTimeString(transferAuthorizationData.startTime)
      const endTimeConvert = this.$datetime.convertTimeObjectToTimeString(transferAuthorizationData.endTime)
      if(transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN && transferAuthorizationData.transferDay.wednesdayYN&&transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && transferAuthorizationData.transferDay.sundayYN){
        this.isSelectAllData =true;
      }
      if ((startTimeConvert == startimesun) && (endTimeConvert == endtimesun || endTimeConvert == '235959999') && transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN && transferAuthorizationData.transferDay.wednesdayYN && transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && transferAuthorizationData.transferDay.sundayYN) {
        this.transferAuthorization.option = '01'
      } else if (startTimeConvert == startimesat && endTimeConvert == endtimesat && transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN && transferAuthorizationData.transferDay.wednesdayYN && transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && !transferAuthorizationData.transferDay.sundayYN) {
        this.transferAuthorization.option = '02'
      }
    },

    isMondayToSun() {
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('235959999'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: true
        }
      }
      return this.designatedHour;
    },

    isMondayToSat() {
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('080000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('180000000'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: false
        }
      }
    },

    handleSelectionChange() {
      if (this.transferAuthorization.option == '01') {
        this.handleTransferHourFromMonToSun()
        this.isfirstTransferAuthorization = '01'
        this.isSecondTransferAuthorization = ''
        this.isthirdTransferAuthorization='';
        this.isSelectAllData = true
        this.isAllowDisSelect = false
        this.isChangeFromStartTime = false
        this.isChangeTransferDay = false;
      } else if (this.transferAuthorization.option == '02') {
        this.handleTransferHourFromMonToSat()
        this.isfirstTransferAuthorization = ''
        this.isSecondTransferAuthorization = '02'
        this.isthirdTransferAuthorization=''
        this.isChangeTransferDay = false;
        this.isChangeFromStartTime = false
        this.isSelectAllData = false
        this.isAllowDisSelect = false
      } else if (this.transferAuthorization.option == '03') {
        if (this.isChangeFromStartTime || this.isChangeFromEndTime) {
          this.designatedHour = this.tempDesignatedHour;
          if (this.transferAuthorizationCode == '02' || this.transferAuthorizationCode == '03') {
            this.isSelectAllData = false;
          } else {
            this.isSelectAllData = true;
          }
        }
        else if (this.isChangeTransferDay == true) {
          return this.designatedHour;
        }
        else {
          this.designatedHour = {
            startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
            endTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
            transferDay: {
              mondayYN: false,
              tuesdayYN: false,
              wednesdayYN: false,
              thursdayYN: false,
              fridayYN: false,
              saturdayYN: false,
              sundayYN: false
            }
          }
          this.isSelectAllData = false;
          this.isfirstTransferAuthorization='';
          this.isSecondTransferAuthorization='';
          this.isthirdTransferAuthorization='03';
        }
      }
    },

    handleTransferHourFromMonToSat() {
      this.isSelectAllData = false
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('080000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('180000000'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: false
        }
      }
    },

    handleTransferHourFromMonToSun() {
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('235959999'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: true
        }
      }
    },

    handleAccountManagement(accountManagementTypeCode: string, mainTransferAccount: string) {
      if (accountManagementTypeCode == '01') {
        this.managementAccount.designatedAccountList.map((account) => {
          if ((account.isWithdraw && account.isInquiry) && account.designatedAccountTypeCode == '00') {
            account.isWithdraw = true;
            account.isInquiry = false
            this.isDisableDesignated.push(false);
          } else {
            this.isDisableDesignated.push(false);
            account.isWithdraw = true;
            account.isInquiry = false;
          }
        })
      } else {
        this.managementAccount.designatedAccountList.map((account) => {
          if (account.accountNo == mainTransferAccount) {
            account.isWithdraw = true;
            account.isInquiry = false;
            this.isDisableDesignated.push(false);
          } else {
            this.isDisableDesignated.push(true);
            account.isWithdraw = false;
            account.isInquiry = false;
          }
        })
      }
    },

    onClickOperation(btn: string) {
      switch (btn) {
        case "cancel":
          this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode: "Question",
            callback: () => {
              this.moveToSubScriptionList();
              modalController.dismiss();
            }
          })
          break;

        case "save": {
          this.isNotCorrectOTPCode = false; // when Invalid Code is displayed then click save again it will desappear
          if (
            this.customerBasicInfo.userID
             && this.customerBasicInfo.email
            && this.transferLimit.transactionLimitAmount && this.transferLimit.dailyLimitAmount
            && this.securityMedia.securityMediaNo && this.handleValidationSecurityEmailFormat(this.securityMedia.securityMediaNo)
            && this.handleValidationCustomerEmailFormat(this.customerBasicInfo.email)
            && !this.isExceedMaximumTransactionLimit && !this.isExceedMaximumDailyLimit
            && this.isComfirmOTPCode
          ) {
            this.$dialog.onOpen({
              content: `${this.$t("message.would_you_like_to_save")}`,
              dialogMode: "Question",
              callback: () => {
                this.handleRegister();
                modalController.dismiss();
              }
            })
          } else {
            this.isInvalidInput = true;
            this.isShowOTPSMSCodeErrorText = true;
            this.$util.scrollToElement('inp_error');
            this.handleValidateComfirmOTPCode();
            this.handleValidationSecurityEmailFormat(this.securityMedia.securityMediaNo) ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true;
            this.handleValidationCustomerEmailFormat(this.customerBasicInfo.email) ? this.isShowEmailErrorText = false : this.isShowEmailErrorText = true;
          }
        }
          break;
      }
    },

    handleValidateComfirmOTPCode() {
      if ((this.authenticationOTPSMSCode.authenticationCode) && !this.isComfirmOTPCode) {
        this.isNotComfirmOTPCode = true;
        this.isShowOTPSMSCodeErrorText = false
      }
      else if (this.authenticationOTPSMSCode.authenticationCode != '' && this.isComfirmOTPCode) {
        this.isNotComfirmOTPCode = false;
      }
    },

    handleGetCommonCodeValue() {
      this.authLineOption = this.$store.state.CommonCodeModule.commonCode.SubscriptionApprovalTypeCode;
      this.authLinePolicyOption = this.$store.state.CommonCodeModule.commonCode.AuthorizationLinePolicyTypeCode;
      this.authLineRoleOption = this.$store.state.CommonCodeModule.commonCode.AuthorizationLineAssignTypeCode;
    },

    async getCustomerInfo(customerNo: string) {
      const reqBody: CIB20100114_REQ_BODY = {
        customerNo: customerNo
      };

      const responseData = (await this.$request.getRequest("CIB20100114", reqBody, false)) as CIB20100114_RES;
      this.customerInformation = responseData.body;
    },

    async handleGenerateUserID() {
      const generateUserID = (await this.$request.getRequest("CIB20100214", {}, false, undefined, false, false)) as CIB20100214_RES;
      this.customerBasicInfo.userID = generateUserID.body.userID;
    },

    handleRemoveFile() {
      this.fileInfo.fileId = '';
      this.fileInfo.fileName = '';
    },

    async getPreWithdrawalAccount() {
      const reqBody: CIB20100212_REQ_BODY = {
        customerNo: `${this.customerNo}`
      };
      // ---- Adapter: CIB20100212_Adapter_PreWithdrawalAccount_InquiryList
      const resBody = await this.$request.getRequest('CIB20100212', reqBody, false) as CIB20100212_RES;

      if (resBody.header.result) {
        this.preWithdrawalAccounts = resBody.body.list;
        this.mainTransferAccounts = resBody.body.list;
        this.managementAccount.designatedAccountList = resBody.body.list.map((account: preWithdrawalAccount) => {
          return {
            accountNo: account.accountNo,
            accountTypeCode: account.accountTypeCode,
            designatedAccountTypeCode: '',
            isInquiry: false,
            isWithdraw: true,
            currencyCode: account.currencyCode
          }
        })

        if (resBody.body.list[0]) {
          this.customerBasicInfo.feeWithdrawalAccount = this.customerBasicInfo.mainTransferAccount = resBody.body.list[0].accountNo;
        }
      }
    },

    async onClickValidateMedia(mediaType: '01' | '02', securityMediaNo: string) {
      switch (mediaType) {
        case '01': {
          if (!this.isWrongPhoneNumberFormat) {
            this.handleSendSMSOTP(securityMediaNo);
          }
        }
          break;

        case '02': {
          let emailValidation = false;
          emailValidation = this.$validate.validateEmail(securityMediaNo);
          if (securityMediaNo) {
            if (emailValidation) {
              this.handleSendSMSOTP(securityMediaNo)
              this.isWrongEmailFormat = false;
            } else if (securityMediaNo && !emailValidation) {
              this.isWrongEmailFormat = true;
            }
          } else {
            this.isWrongEmailFormat = true
          }
        }
          break;

        default:
          break;
      }
    },

    handleValidationSecurityEmailFormat(securityEmail: string): boolean {
      let validateSecurityEmailFormat = false;
      if (this.securityMedia.mediaType == '02') {
        validateSecurityEmailFormat = this.$validate.validateEmail(securityEmail);
        validateSecurityEmailFormat ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true;
      } else {
        validateSecurityEmailFormat = true;
      }
      return validateSecurityEmailFormat;
    },

    handleValidationCustomerEmailFormat(customerEmail: string): boolean {
      let validateCustomerEmailFormat = false;
      validateCustomerEmailFormat = this.$validate.validateEmail(customerEmail);
      validateCustomerEmailFormat ? this.isShowEmailErrorText = false : this.isShowEmailErrorText = true;
      return validateCustomerEmailFormat;
    },

    handleStopCountdown() {
      this.authenticationOTPSMSCode.authenticationCode = '';
      this.isDisableComfirmOTPCodeButton = true;
      this.isNotComfirmOTPCode = false;
      this.isNotCorrectOTPCode = false;
    },

    async handleSendSMSOTP(securityMediaNo: string) {
      const reqBody: CIB20100222_REQ_BODY = {
        phoneNumber: securityMediaNo,
        userID: `${this.userID}`,
        countryID: 'Cambodia'
      };

      const responseData = await this.$request.getRequest('CIB20100222', reqBody, false) as CIB20100222_RES;

      if (responseData.header.result == true) {
        this.isCountDown = true;
        this.authenticationOTPSMSCode.authTransactionDate = responseData.body.authTransactionDate;
        this.authenticationOTPSMSCode.authTransactionSeqNo = responseData.body.authTransactionSeqNo;
      }
    },

    async handleComfirmOTMSMSCode() {
      const reqBody: CIB20100223_REQ_BODY = {
        authenticationCode: this.authenticationOTPSMSCode.authenticationCode,
        authTransactionDate: this.authenticationOTPSMSCode.authTransactionDate,
        authTransactionSeqNo: this.authenticationOTPSMSCode.authTransactionSeqNo
      };

      const responseData = await this.$request.getRequest('CIB20100223', reqBody, false) as CIB20100223_RES;

      if (responseData.body.successYN == 'Y') {
        this.isComfirmOTPCode = true;
        this.isNotComfirmOTPCode = false;
        this.isCountDown = false;
        this.isCorrectOTPCode = true;
      } else {
        this.isNotCorrectOTPCode = true;
        this.isNotComfirmOTPCode = false;
      }
    },

    async getPreTransferLimit() {
      const reqBody: CIB20100213_REQ_BODY = {
        limitTypeCode: "00",
        limitCategoryCode: "01",
        transactionCurrencyCode: ""
      };

      // ---- Adapter: CIB20100213_Adapter_PreTransferLimit_InquiryDetail
      const resBody = await this.$request.getRequest('CIB20100213', reqBody, false) as CIB20100213_RES;

      if (resBody.header.result) {
        resBody.body.list.forEach((limit: TransactionLimit) => {
          if (limit.limitTypeCode === '00') {
            this.transferLimit.maxBankTransactionLimit = limit.maxTransactionAmount;
            this.transferLimit.maxBankDailyLimit = limit.maxDailyAmount;
            this.transferLimit.transactionLimitAmount = limit.defaultMaxTransactionAmount;
            this.transferLimit.dailyLimitAmount = limit.defaultMaxDailyAmount
          }
        })
      }
    },

    async handleRegister() {
      await this.handleFileRegister().then(async res => {
        if (res === false) {
          this.$dialog.onOpen({
            content: `${this.$t("Error File Upload!")}`, // Network Error, None File, Server not working
            dialogMode: "Warning",
            callback: () => {
              modalController.dismiss();
            }
          })
        } else {
          this.designatedAccountListForRegister = []; // reset all data to empty before click save again (prevent duplicate data)
          this.managementAccount.designatedAccountList.map((account: { accountNo: string, accountTypeCode:string, designatedAccountTypeCode: string, isInquiry: boolean, isWithdraw: boolean, currencyCode: string }) => {

            if( account.isInquiry == true && account.isWithdraw == true){
              account.designatedAccountTypeCode = "01"
            }else if(account.isInquiry == true){
              account.designatedAccountTypeCode = "02"
            }else if(account.isWithdraw == true){
              account.designatedAccountTypeCode = "01"
            }else if(!account.isInquiry || !account.isInquiry){
              account.designatedAccountTypeCode = ""
            }

            this.designatedAccountListForRegister.push({
              accountNo: account.accountNo,
              designatedAccountTypeCode: account.designatedAccountTypeCode,
              currencyCode: account.currencyCode,
              accountTypeCode: account.accountTypeCode
            })

            // console.log("this.this.fileList",this.fileList);
            // this.fileList.map((item)=>{
            //   // if(item.fileId == '' && item.filePath == ''){
            //   //   this.fileList = [];
            //   // }else{
            //     this.fileList.push({
            //       fileId: this.fileInfo.fileId,
            //       filePath: this.fileInfo.filePath
            //     })
            //   // }
            // });
          });
          const reqBody: CIB20100221_REQ_BODY = {
            transferAuthorizationHour: {
              operationServiceCode: this.transferAuthorization.option,
              operationServiceStartTime: this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime),
              operationServiceEndTime: this.$datetime.convertTimeObjectToTimeString(this.designatedHour.endTime),
              operationMondayYN: this.booleanToString(this.designatedHour.transferDay.mondayYN),
              operationTuesdayYN: this.booleanToString(this.designatedHour.transferDay.tuesdayYN),
              operationWednesdayYN: this.booleanToString(this.designatedHour.transferDay.wednesdayYN),
              operationThursdayYN: this.booleanToString(this.designatedHour.transferDay.thursdayYN),
              operationFridayYN: this.booleanToString(this.designatedHour.transferDay.fridayYN),
              operationSaturdayYN: this.booleanToString(this.designatedHour.transferDay.saturdayYN),
              operationSundayYN: this.booleanToString(this.designatedHour.transferDay.sundayYN),
            },
            userInfo: {
              userID: this.customerBasicInfo.userID,
              customerName: this.customerInformation.customerFullName,
              customerNo: this.customerInformation.customerNo,
              languageCode: 'En',
              accountManagementTypeCode: this.managementAccount.accountManagementTypeCode,
              subscriptionApprovalTypeCode: this.companyAuthorizationLine.authLineOption,
              authorizationLineAssignTypeCode: this.companyAuthorizationLine.authLineRoleOption,
              authorizationLinePolicyTypeCode: this.companyAuthorizationLine.authLinePolicyOption,
              cellPhoneNo: this.securityMedia.securityMediaNo,
              customerType: this.customerInformation.customerType,
              email: this.customerBasicInfo.email,
              feeWithdrawalAccountNo: this.customerBasicInfo.feeWithdrawalAccount,
              fileId: 'fileid',
              mainTransferAccount: this.customerBasicInfo.mainTransferAccount,
              mainUserRoleID: `${this.companyAuthorizationLine.authLineOption == '01' ? 'FINAL' : 'STP'}`,
              mobileISDCode: '885',
              securityMediaNo: this.securityMedia.securityMediaNo,
              securityMediaTypeCode: this.securityMedia.mediaType,
            },
            fileList: this.fileList,
            designatedAccountList: this.designatedAccountListForRegister,
            transactionLimitList: this.handleTransferLimit(),
          };

          const resBody = await this.$request.getRequest('CIB20100221', reqBody, false) as CIB20100221_RES;
          if (this.approvalType) {
          this.moveToSubScriptionList();
          }
    // 
          if (resBody.header.result) {
            this.moveToSubScriptionList();
            this.$dialog.onOpen({
              dialogMode: 'Success',
              content: this.$t("message.register_success"),
              callback: () => {
                modalController.dismiss();
              }
            })
          }
        }
      })
    },

    async handleFileRegister(): Promise<void | boolean> {
      if (this.getFile !== null) {
        const res = await this.$file.uploadFile([this.getFile], "CIB");
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.fileInfo.filePath = resFileInfo.data.body.filePath
            this.fileInfo.fileId = res.data.body.fileList[i].fileId
          }
        } else {
          return false
        }
        this.fileList.push({fileId: this.fileInfo.fileId, filePath: this.fileInfo.filePath})
      }
    },

    async handleUploadFile(event: any) {
      // const files = event.target.files;
      // const file = files[0];

      // this.getFile = file;
      // const size = fileSize.countFileSize(file.size);
      // if (file.size > 10485760) {
      //   this.$dialog.onOpen({
      //     content: this.$t('message.file_size_must_not_exceed_10MB'),
      //     dialogMode: "Warning",
      //     callback: () => {
      //       modalController.dismiss()
      //     },
      //   });
      // }else{
      //   this.fileInfo.fileName = file.name;
      //   this.fileInfo.fileSize = size;
      //   const responseFileUpload = await this.$file.uploadFile(files, "CIB");

      //   for(let i = 0; i < responseFileUpload.data.body.fileList.length; i++) {
      //     this.fileInfo.fileId = responseFileUpload.data.body.fileList[i].fileId;
      //   }

      //   const responseFileInfo = await this.$file.getFileInfo(this.fileInfo.fileId);
      //   this.fileInfo.filePath = responseFileInfo.data.body.filePath;

      //   this.fileList.push({
      //     fileId: this.fileInfo.fileId,
