        this.fileInfo.fileName = file.name;
      }
      const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
      duplicatedFile.value = ''
    },

    handlePasteEmailValue(event) {
      let pasteEmailData = event.clipboardData.getData('text');
      if (pasteEmailData) {
        this.isWrongEmailFormat = false;
      }
    },

    handlePasteCustomerEmailValue(event) {
      let pasteEmailData = event.clipboardData.getData('text');
      if (pasteEmailData) {
        this.isShowEmailErrorText = false;
      }
    },

    moveToSubScriptionList() {
      this.$router.push('CIB201001SC00');
    },

    isInquiryChange(index: number) {
      if (this.managementAccount.accountManagementTypeCode == '01') {
        if (this.managementAccount.designatedAccountList[index].isInquiry == true) {  // if checked the value = true
          this.managementAccount.designatedAccountList[index].isInquiry = false;  // set viewOnly checkbox to checked
          this.managementAccount.designatedAccountList[index].isWithdraw = true;
          this.managementAccount.designatedAccountList[index].designatedAccountTypeCode = "00";
        }
      } else {
        if (this.managementAccount.designatedAccountList[index].isInquiry == true) {  // if checked the value = true
          this.managementAccount.designatedAccountList[index].isInquiry = true;  // set viewOnly checkbox to checked
          this.managementAccount.designatedAccountList[index].isWithdraw = false;
          this.managementAccount.designatedAccountList[index].designatedAccountTypeCode = "02";
        }
      }
    },

    isWithdrawChange(index: number) {
      if (this.managementAccount.accountManagementTypeCode == '02') {
        if (this.managementAccount.designatedAccountList[index].isWithdraw == true) {  // if checked the value = true
          this.managementAccount.designatedAccountList[index].isWithdraw = true;  // set viewOnly checkbox to checked 
          this.managementAccount.designatedAccountList[index].isInquiry = false;
          this.managementAccount.designatedAccountList[index].designatedAccountTypeCode = "00";
        }
      } else {
        if (this.managementAccount.designatedAccountList[index].isWithdraw == true) {  // if checked the value = true
          this.managementAccount.designatedAccountList[index].isWithdraw = true;  // set viewOnly checkbox to checked 
          this.managementAccount.designatedAccountList[index].isInquiry = true;
          this.managementAccount.designatedAccountList[index].designatedAccountTypeCode = "01";
        }
      }
    },

    handleManagementAccount() {
      const designatedAccount = this.managementAccount.designatedAccountList.map((account: { accountNo: string, designatedAccountTypeCode: string, isInquiry: boolean, isWithdraw: boolean, currencyCode: string }) => {
        return {
          accountNo: account.accountNo,
          designatedAccountTypeCode: account.designatedAccountTypeCode,
        }
      })
      return designatedAccount;
    },

    handleTransferLimit() {
      const bankTransferLimit = [{
        transactionLimitAmount: Number(this.transferLimit.transactionLimitAmount),
        dailyLimitAmount: Number(this.transferLimit.dailyLimitAmount),
        limitCategoryCode: "01",
        limitTypeCode: "00",
        transactionCurrencyCode: "USD"
      }]
      return bankTransferLimit;
    },

    stringToBoolean(YN: string) {
      return YN === 'Y' ? true : false
    },

    booleanToString(YN: boolean) {
      return YN ? "Y" : "N";
    }
  }
})
